import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'registration-form',
  templateUrl: './registration-form.component.html',
  styleUrls: ['./registration-form.component.css']
})
export class RegistrationFormComponent  {

  registrationForm!: FormGroup;

  constructor(private fb: FormBuilder) {}

  ngOnInit() {
    this.registrationForm = this.fb.group({
      firstName : ['', Validators.required],
      lastName : ['', Validators.required],
      email : ['', [Validators.required, Validators.email]],
      phoneNumber : ['', Validators.required],
      password : ['', [Validators.required,  Validators.pattern('[a-z0-9.@]*'), Validators.minLength(6)]],
      confirmPassword : ['', [Validators.required, Validators.pattern('[a-z0-9.@]*'), Validators.minLength(6)]],
      checkBox : ['', Validators.required]

    })
  }
  onSubmit(form: FormGroup) {
    console.log('Valid?', form.valid); // true or false
    console.log('First Name', form.value.firstName);
    console.log('Last Name', form.value.lastName);
    console.log('Email', form.value.email);
    console.log('Phone Number', form.value.phoneNumber);
    console.log('Password', form.value.password);
    console.log('Confirm Password', form.value.confirmPassword);
  }

}
